.menu {
  width: 100%;
  height: 93.5vh;
  position: absolute;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  z-index: 1;
  background: #000;
  -webkit-animation-name: growDown;
          animation-name: growDown;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}
.menuClose {
  width: 100%;
  height: 0vh;
  position: absolute;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  z-index: 1;
  background: #000;
  -webkit-animation-name: growUp;
          animation-name: growUp;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}
.menu_top {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding: 5px 30px;
}
.menu_close_btn {
  background: transparent;
  font-size: 2rem;
  font-family: "Blinker", sans-serif;
  border: none;
  color: #fff;
