}

.menu_bottom {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 100%;
}

.menu_bottom_left {
  width: 50%;
  padding: 0 100px;
  height: 100%;
}
.menu_bottom_right {
  width: 50%;
  height: 100%;
}

.menu_ul {
  list-style-type: none;
}
.menu_li {
  margin-top: 7px;
}
.menu_bottom_left h3 {
  color: rgb(241, 241, 28);
  font-size: 3rem;
}
.menu_links {
  color: white;
  font-family: "Blinker", sans-serif;
  font-size: 2.5rem;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.menu_links:hover {
  margin-left: 10px;
  color: rgb(241, 241, 28);
}

@-webkit-keyframes growDown {
  0% {
    height: 0vh;
  }
  100% {
    height: 93.5vh;
  }
}

@keyframes growDown {
  0% {
    height: 0vh;
  }
  100% {
    height: 93.5vh;
  }
}

@-webkit-keyframes growUp {
  0% {
    height: 93.5vh;
  }
  100% {
    height: 0vh;
  }
}

@keyframes growUp {
  0% {
    height: 93.5vh;
  }
  100% {
    height: 0vh;
  }
}

