/* ===================== CREATE FORM ========================== */
.create_form_div {
  width: 100%;
  height: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background: #fff;
}

.create_form {
  border-radius: 5px;
  width: 90%;
  margin: 0 auto;
  height: 100%;
  color: #000;
}
.create_form h1 {
  text-align: center;
  font-size: 3rem;
  color: #fff;
}

.create_form form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 20px 20px;
  width: 100%;
}
.create_form_top_input {
  width: 100%;
  margin-bottom: 18px;
}
.uploaded_image {
  width: 100%;
  height: 400px;
}
.uploaded_image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 15px;
}

input[type="file"] {
  position: absolute;
  z-index: -1;
  top: 10px;
  left: 8px;
  font-size: 17px;
  color: #b8b8b8;
}

.button-wrap {
  position: relative;
}

.button-wrap .button {
  display: inline-block;
  padding: 12px 18px;
  cursor: pointer;
  border-radius: 5px;
  background-color: #8ebf42;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  font-family: "Crete Round", serif;
}

.form_controls {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
}
.form_controls label {
  font-family: "Markazi Text", serif;
  font-size: 1.5rem;
}
#fileinput {
  border: none;
  font-size: 1.1rem;
  cursor: pointer;
  background: rgb(241, 241, 28);
  width: 100px;
  padding: 10px 10px;
}

.create_form_middle_input {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 18px;
}
.create_form_middle_input .form_controls {
  width: 48%;
}

.form_controls input {
  border: 2px solid grey;
  height: 40px;
  border-radius: 5px;
  font-size: 20px;
  padding: 0px 5px;
  background: transparent;
  color: #000;
  transition: all 0.3s ease-in-out;
}
.form_controls input:focus {
  border: 2px solid rgb(241, 241, 28);
  outline: none;
}
.create_form_bottom_input {
  margin-top: 20px;
}
.form_controls textarea {
  border: 2px solid grey;
  border-radius: 5px;
  background: transparent;
  font-size: 20px;
  padding: 5px 5px;
  color: #000;
  transition: all 0.3s ease-in-out;
}
.form_controls textarea:focus {
  border: 2px solid rgb(241, 241, 28);
  outline: none;
}
.create_btn {
  width: 120px;
  font-size: 1.5rem;
  background: rgb(241, 241, 28);
  border: 2px solid rgb(241, 241, 28);
  cursor: pointer;
  text-align: center;
  -webkit-align-self: center;
          align-self: center;
  margin-top: 20px;
  height: 40px;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}
.create_btn:hover {
